.title-side-bar {
    color: #ffffff;
    font-family: Quicksand;
    font-size: 14px;
    font-style: normal;
    font-stretch: normal;
    font-weight: 700;
    line-height: 19px;
    text-align: left;
    text-transform: none;
    text-decoration: none;
    letter-spacing: 0px;
}


.active-link {

    -webkit-border-image: -webkit-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);

         -o-border-image: -o-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);

            border-image: linear-gradient(-134deg, #DD166B 0%, #722FD9 100%);
    border-image-slice: 1;
    border-width: 3px;

}


