.home-layout {
    background-image: -webkit-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
    background-image: -o-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
    background-image: linear-gradient(-134deg, #DD166B 0%, #722FD9 100%);

    margin-left: 230px;
    /* width: calc(100%-210px); */
    padding: 0px 20px;
    height: 100%;
    position: fixed;
    overflow: auto;
    width: 100%;




}

.home-layout-inner {

    padding: 0px 20px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    



}
.title-home-layout{
   
    color: #ffffff;
    font-family: Quicksand;
    font-size: 60px;
    font-style: normal;
    font-stretch: normal;
    font-weight: 700;
    line-height: 27px;
    text-align: left;
    text-transform: none;
    text-decoration: none;
    letter-spacing: 0px;
}


