@-webkit-keyframes Logo-app-loading {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  @keyframes Logo-app-loading {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  .Logo-app-loading {
    -webkit-animation: Logo-app-loading infinite 1s linear;
            animation: Logo-app-loading infinite 1s linear;
    
  }


