

.text-title {
  color: #ffffff;
  font-family: Quicksand;
  font-size: 25px;
  font-style: normal;
  font-stretch: normal;
  font-weight: 700;
  line-height: 33px;
  text-align: center;
  text-transform: none;
  text-decoration: none;
  letter-spacing: 0px;
}


.main {
  background-image: -webkit-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
  background-image: -o-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
  background-image: linear-gradient(-134deg, #DD166B 0%, #722FD9 100%);

  margin-left: 210px;
  width: calc(100%-200px);
  padding: 0px 20px;
  height: 100%;
  position: fixed;
  overflow: auto;

 
  

}
/*  */

.active-link {
  border-left: 5px solid #DD166B;
  -webkit-border-image: -webkit-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
       -o-border-image: -o-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
          border-image: linear-gradient(-134deg, #DD166B 0%, #722FD9 100%);
  border-image-slice: 1;
  border-width: 5px;
  

}

.title-select-genres{
  width: 820px;
  height: 75px;
  color: #ffffff;
  font-family: Quicksand;
  font-size: 40px;
  font-style: normal;
  font-stretch: normal;
  font-weight: 700;
  line-height: 53px;
  text-align: left;
  text-transform: none;
  text-decoration: none;
  letter-spacing: 0px;

}
.browser-router{
  background-image: -webkit-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
  background-image: -o-linear-gradient(224deg, #DD166B 0%, #722FD9 100%);
  background-image: linear-gradient(-134deg, #DD166B 0%, #722FD9 100%);

}


